import React, { Component } from 'react'

import CssBaseline from '@material-ui/core/CssBaseline'

import './App.css'

import Sidebar from './Components/Sidebar'
import Alerts from './Alerts/Alerts'
import InputMedicine from './Registry/InputMedicine'
import OutputMedicine from './Registry/OutputMedicine'
import Calendar from './Calendar'

class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      currentScreen: 'Alerts'
    }
  }

  changeScreen (screen) {
    this.setState({ currentScreen: screen })
  }

  render () {
    return (
      <div className='App'>
        <CssBaseline />
        <Sidebar
          changeScreen={this.changeScreen.bind(this)}
        />
        <div style={{ marginTop: '75px' }}>
          {this.state.currentScreen === 'Alerts'
            ? <Alerts />
            : null}
          {this.state.currentScreen === 'InputMedicine'
            ? <InputMedicine />
            : null}
          {this.state.currentScreen === 'OutputMedicine'
            ? <OutputMedicine />
            : null}
          {this.state.currentScreen === 'Calendar'
            ? <Calendar />
            : null}
        </div>
      </div>
    )
  }
}

export default App
