import React, { useState } from 'react'

import Paper from '@material-ui/core/Paper'
import Avatar from '@material-ui/core/Avatar'
import Select from '@material-ui/core/Select'
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import { Grid, Typography, MenuItem } from '@material-ui/core'
import InputAdornment from '@material-ui/core/InputAdornment'
import LinearProgress from '@material-ui/core/LinearProgress'

export default function InputMedicine (props) {
  const [medicineName, setMedicineName] = useState('')
  const [lot, setLot] = useState('')
  const [cost, setCost] = useState('')
  const [presentation, setPresentation] = useState('Ampolleta 40')
  const [expiration, setExpiration] = useState(Date.now())
  let dateOptions = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
  let now = new Date()
  return (
    <Paper
      style={{
        margin: 15
      }}
    >
      <form
        style={{
          display: 'flex',
          flexDirection: 'column',
          padding: 15
        }}>
        <Typography variant='h4' align='left'>
             Registro de Entrada
        </Typography>
        <Grid container>
          <Grid item md={6}>
            <TextField
              required
              fullWidth
              id='nombre'
              label='Nombre'
              onChange={(e) => setMedicineName(e.target.value)}
              value={medicineName}
              margin='normal'
              type='text'
            />
          </Grid>
          <Grid item md={6}>
            <Select
              required
              fullWidth
              id='nombre'
              label='Presentación'
              onChange={(e) => setPresentation(e.target.value)}
              value={presentation}
              type='text'
              margin='none'
            >
              <MenuItem value={'Ampolleta 40'}>
               Ampolleta 40
              </MenuItem>
              <MenuItem value={'Ampolleta 60'}>
               Ampolleta 60
              </MenuItem>
              <MenuItem value={'Ampolleta 100'}>
               Ampolleta 100
              </MenuItem>
            </Select>
          </Grid>
          <Grid item md={6}>
            <TextField
              required
              fullWidth
              id='lote'
              label='Lote'
              onChange={(e) => setLot(e.target.value)}
              value={lot}
              margin='normal'
              type='text'
            />

          </Grid>
          <Grid item md={6}>
            <TextField
              required
              fullWidth
              id='costo'
              label='Costo'
              onChange={(e) => setCost(e.target.value)}
              value={cost}
              margin='normal'
              type='number'
              InputProps={{
                startAdornment: <InputAdornment position='start'>$</InputAdornment>
              }}
            />
          </Grid>
          <Grid item md={6}>
            <TextField
              required
              fullWidth
              id='fecha vencimiento'
              label='Fecha Vencimiento'
              onChange={(e) => setExpiration(e.target.value)}
              value={expiration}
              type='date'
              margin='normal'
            />
          </Grid>
          <Grid item md={12}>
            <div
              style={{
                margin: 15
              }}>
              <Grid container>
                <Grid item md={4}>
                  <Paper style={{ margin: 5 }}>
                    <Grid container>
                      <Grid item md={3}>
                        <Avatar
                          alt='Remy Sharp'
                          src='./avatar.jpg'
                          style={{ margin: 'auto', marginTop: 5 }} />
                      </Grid>
                      <Grid item md={9}>
                        <Typography variant='h6'>
                        Juan Pablo Montoya
                        </Typography>
                        <Typography >
                      Farmacólogo
                        </Typography>
                      </Grid>
                    </Grid>
                  </Paper>
                </Grid>
                <Grid item md={4}>
                  <Paper
                    style={{ margin: 5 }}>
                    <Typography variant='h6'>
                Fecha de Ingreso
                    </Typography>
                    <Typography >
                      {now.toLocaleString('es-ES', dateOptions)}
                    </Typography>
                  </Paper>
                </Grid>
                <Grid item md={4}>
                  <Paper style={{ margin: 5 }}>
                    <Typography variant='h6'>
                Sensor RFID
                    </Typography>
                    <Typography >
                Esperando Sensor...
                    </Typography>
                    <LinearProgress />
                  </Paper>
                </Grid>
              </Grid>
            </div>
          </Grid>
        </Grid>
        <Button variant='contained'>
           Enviar
        </Button>
      </form>
    </Paper>
  )
}
